
import { defineComponent, PropType } from "vue";

export default defineComponent({
  name: "MyOrderCard",
  props: {
    imgUrl: {
      type: String as PropType<string>,
      default: "",
    },
    location: {
      type: String as PropType<string>,
      default: "",
    },
    price: {
      type: Number as PropType<number>,
      default: 0,
    },
    title: {
      type: String as PropType<string>,
      default: "",
    },
  },
});
